export default {
  props: {
    chartData: {
      type: Array,
      default: () => []
    },
    titleName: {
      type: String,
      default: ''
    },
    param: {
      type: Object
    }
  },
  data() {
    return {};
  },
  computed: {

  },
  watch: {
  },
  created() {},
  mounted() {
    console.log(this.chartData);
    const { dateType, MTBFVal, MTTRVal } = this.param;
    const unit = dateType === 'month' ? '日' : '月';
    const mtBfValue = [];
    const mtTrValue = [];
    const xData = [];
    const mtBfStandard = [];
    const mtTrStandard = [];
    this.chartData.forEach(item => {
      mtBfValue.push(item.mtBfValue.toFixed(2));
      mtTrValue.push(item.mtTrValue.toFixed(2));
      xData.push(item.index + unit);
      mtBfStandard.push(MTBFVal);
      mtTrStandard.push(MTTRVal);
    });
    this.draw(xData, mtBfValue, mtTrValue, mtBfStandard, mtTrStandard, this.titleName);
  },
  methods: {
    draw(xData, mtBfValue, mtTrValue, mtBfStandard, mtTrStandard, titleName) {
      this.myChart = this.$echarts.init(this.$refs.echartGantt);
      this.myChart.setOption({
        tooltip: {
          trigger: 'axis',
          // axisPointer: {
          //   type: 'cross',
          //   crossStyle: {
          //     color: '#999'
          //   }
          // },
          formatter: (params) => {
            // const seriesNames = ['故障时间', '运行时间', 'MTBF', 'MTTR'];
            let str = params[0].name;
            params.forEach((item, index) => {
              str += `<br>${item.marker}${item.seriesName}: ${item.value}min`;
              // if (seriesNames.includes(item.seriesName)) {
              //   str += `<br>${item.seriesName}: ${item.value}`;
              // } else {
              //   str += `<br>${item.seriesName}: ${item.value}(min)`;
              // }
            });
            return str;
          }
        },
        title: {
          text: titleName,
          x: 'center',
          y: 'top',
          textAlign: 'left'
          // textAlign: 'center'
        },
        toolbox: {
          show: true,
          feature: {
            saveAsImage: {
              show: true
            }
          }
        },
        legend: {
          right: 40
        },
        grid: {
          containLabel: true,
          left: '3%',
          right: '3%'
        },
        dataZoom: [
          {
            type: 'slider',
            filterMode: 'weakFilter',
            top: 'bottom'
          }
          // {
          //   type: 'inside',
          //   filterMode: 'weakFilter'
          // }
        ],
        xAxis: [
          {
            type: 'category',
            data: xData,
            axisPointer: {
              type: 'shadow'
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: 'min',
            min: 0
            // max: 250,
            // interval: 50,
            // axisLabel: {
            //   formatter: (value, index) => {
            //     return parseInt(value / 60 / 60);
            //   }
            // }
          },
          {
            type: 'value',
            name: 'min',
            min: 0
            // max: 100,
            // interval: 5,
            // axisLabel: {
            //   formatter: '{value} %'
            // }
          }
        ],
        series: [
          {
            name: 'MTBF',
            type: 'bar',
            itemStyle: {
              normal: {
                color: '#9B3768'
              }
            },
            label: {
              show: true,
              position: 'top'
            },
            barWidth: '30',
            data: mtBfValue
          },
          {
            name: 'MTBF标准线',
            type: 'line',
            itemStyle: {
              normal: {
                color: '#D90D00'
              }
            },
            smooth: false,
            symbolSize: 0.01,
            data: mtBfStandard
          },
          {
            name: 'MTTR',
            type: 'line',
            yAxisIndex: 1,
            smooth: true,
            symbolSize: 5,
            itemStyle: {
              normal: {
                color: '#e2e205'
              }
            },
            label: {
              show: true,
              position: 'top',
              distance: 15
            },
            data: mtTrValue
          },
          {
            name: 'MTTR标准线',
            type: 'line',
            yAxisIndex: 1,
            smooth: true,
            symbolSize: 0.01,
            itemStyle: {
              normal: {
                color: '#8ECC4B'
              }
            },
            data: mtTrStandard
          }
        ]
      });
    },
    timeStampSeconds(cellValue) {
      var day = parseInt(cellValue / 60 / 60 / 24);
      var hour = parseInt(cellValue / 60 / 60 % 24);
      var min = parseInt(cellValue / 60 % 60);
      var s = parseInt(cellValue % 60);
      cellValue = '';
      if (day > 0) {
        cellValue = day + '天';
      }
      if (hour > 0) {
        cellValue += hour + '小时';
      }
      if (min > 0) {
        cellValue += parseFloat(min) + '分钟';
      }
      if (s > 0) {
        cellValue += parseFloat(s) + '秒';
      }
      return cellValue;
    }
  }
};
