import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT
} from '@/utils/constantsFile';
// 导入所需接口
// import { GetList, WarningStatistics } from '@/api/equipmentmonitoring';
import { deviceExceptionGetDeviceExceptionComparedAnalyze } from '@/api/warn';
export default {
  components: {},
  props: {
    tableInfo: {
      type: Array,
      default: () => []
    },
    param: {
      type: Object
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      deviceName: '',
      lineOptions: [],
      groupOptions: [],
      // table是否加载
      tableLoadings: true,
      // table数据
      tableData: [],
      // table表头配置
      tableColumn: [
        {
          field: 'title',
          title: '',
          visible: true,
          width: '160',
          align: 'center',
          fixed: 'left'
        }
      ],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      }
    };
  },
  computed: {

  },
  watch: {
  },
  created() {
    // this.findList();
    console.log(this.param);
    const { dateType, dateVal, MTBFVal, MTTRVal } = this.param;
    const unit = dateType === 'month' ? '日' : '月';
    if (dateType === 'month') {
      this.tableColumn[0].title = dateVal?.substring(5, 7) + '月';
    } else {
      this.tableColumn[0].title = dateVal?.substring(0, 4) + '年';
    }
    const clumns = this.tableInfo.map(item => ({
      field: item.index,
      title: item.index + unit,
      width: '100',
      align: 'center'
    }));
    this.tableColumn = [...this.tableColumn, ...clumns];
    const tableData = [{
      title: 'MTBF(min)'
    },
    {
      title: 'MTTR(min)'
    },
    {
      title: 'MTBF标准线'
    },
    {
      title: 'MTTR标准线'
    }];
    this.tableInfo.forEach(item => {
      tableData[0][item.index] = item.mtBfValue.toFixed(2);
      tableData[1][item.index] = item.mtTrValue.toFixed(2);
      tableData[2][item.index] = MTBFVal;
      tableData[3][item.index] = MTTRVal;
    });
    this.tableData = tableData;
    console.log(this.tableColumn);
  },
  methods: {
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      var searchContent = {
        conditionJson1: [
          {
            column: 'startTime',
            value: this.startTime
          },
          {
            column: 'endTime',
            value: this.endTime
          }
        ]
      };
      if (this.typeValue === 0) {
        searchContent.conditionJson1.push({
          column: 'groupId',
          operator: 1,
          ptype: 'int',
          type: 'eq',
          value: this.modelValue
        });
      } else {
        searchContent.conditionJson1.push({
          column: 'deviceCode',
          operator: 1,
          ptype: 'string',
          type: 'eq',
          value: this.modelValue
        });
      }

      try {
        const res = await deviceExceptionGetDeviceExceptionComparedAnalyze({}, searchContent);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将数据写入表单
          this.tableDatas = res.rows;

          var arr = res.rows;
          var arrJson = [{
            title: '故障时间'
          }, {
            title: '故障次数'
          }, {
            title: '运行时间'
          }, {
            title: 'MTBF'
          }, {
            title: 'MTTR'
          }];

          for (var i = 0; i < arr.length; i++) {
            this.tableColumns.push({
              field: arr[i].deviceCode + 'task',
              title: arr[i].deviceName,
              visible: true,
              minWidth: '200',
              align: 'center'
            });

            arrJson[0][arr[i].deviceCode + 'task'] = this.timeStampSeconds(arr[i].warnAllTime);
            arrJson[1][arr[i].deviceCode + 'task'] = arr[i].warnAllCount + ' 次';
            arrJson[2][arr[i].deviceCode + 'task'] = this.timeStampSeconds(arr[i].runTime);
            arrJson[3][arr[i].deviceCode + 'task'] = this.timeStampSeconds(arr[i].mtbf);
            arrJson[4][arr[i].deviceCode + 'task'] = this.timeStampSeconds(arr[i].mttr);
          }
          this.tableDatas = arrJson;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    exportTable() {
      // this.$refs.xGrid.exportData('MTTR&MTBF统计报表', this.tableDatas);
      this.$refs.xGrid.exportData({
        filename: 'MTTR&MTBF统计报表'
      });
    },
    timeStampSeconds(cellValue) {
      var day = parseInt(cellValue / 60 / 60 / 24);
      var hour = parseInt(cellValue / 60 / 60 % 24);
      var min = parseInt(cellValue / 60 % 60);
      var s = parseInt(cellValue % 60);
      cellValue = '';
      if (day > 0) {
        cellValue = day + '天';
      }
      if (hour > 0) {
        cellValue += hour + '小时';
      }
      if (min > 0) {
        cellValue += parseFloat(min) + '分钟';
      }
      if (s > 0) {
        cellValue += parseFloat(s) + '秒';
      }
      return cellValue;
    }
  }
};
